#search {
	min-width: 320px;
    max-width: 640px;
	width: 100%;
	height: 45px;
	box-sizing: border-box;
	padding: 5px 20px;
	z-index: 10;
	background-color: rgba(234, 44, 44, 0);
	opacity: 1;
}

.search {
    margin: 0 auto;
    width: 100%;
    height: 100%;
    text-align: center;
    top: 0;
}

.sl {
	left: 0;
	height: 100%;
	margin-top: 5px;
}

.sl i {
	width: 56px;
    height: 21px;
    display: inline-block;
	background: url(/static/media/jd-sprites.1c93a187.png) no-repeat;
    background-size: 200px 200px;
    background-position: 0 -109px;
}

.frc {
	margin-left: 66px;
	margin-right: 40px;
	background-color: #fff;
	border: 1px solid #fff;
	height: 30px;
	border-radius: 30px;
	margin-top: 3px;
	box-sizing: border-box;
}

.searchicon {
	left: 7px;
	top: 3px;
	display: inline-block;
	width: 20px;
	height: 20px;
	background: url(/static/media/jd-sprites.1c93a187.png) no-repeat;
	background-position: -60px -109px;
	background-size: 200px 200px;
}

.frc form {
	height: 100%;
}

.frc input {
	border: 0;
	width: 98%;
	height: 100%;
	color: #333;
	text-indent: 30px;
	font-size: 18px;
	border-radius: 30px;
}

.sub {
	top: 0;
	right: 0;
	width: 30px;
	height: 100%;
	line-height: 40px;
	font-size: 15px;
	color: #fff;
	margin-top: -5px;
}
html,
body {
    position: relative;
    height: 100%;
    width: 100%;
}

html,
body {
    font-family: "Microsoft YaHei", arial, sans-serif;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    margin: 0;
    padding: 0;
    color: #333;
    width: 100%;
    background: #fff;
}

input,
ul,
li,
p,
img,
h1,
h2,
h3 {
    margin: 0;
    padding: 0;
}

img {
    width: 100%;
    height: 100%;
}


* {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-touch-callout: none;
}
a,
input,
textarea,
select {
    outline: 0;
}

p {
    margin: 0;
    overflow: hidden;
    padding: 0;
}

div {
    word-break:break-all;
}

i {
    font-style:normal;
}

ul, li,  dl, dt, dd {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.fl {
    float: left;
}

.fr {
    float: right;
}

.fc {
    clear: both;
}

.pr {
    position: relative;
}

.pa {
    position: absolute;
}

.pf {
    position: fixed;
}

ul.navigation_menu > li{
    background: #587758;
    color: #333;
    text-align: center;
    border: solid gray 1px;
}


.nav-bar {
	display: flex;
	justify-content: space-between;
	font-size: 20px;
	margin: 0 20px;
}

.nav-bar-container {
	position: fixed;
    bottom: 0px;
    background: white;
    width: 100%;
}

.nav-bar > a {
	text-decoration: none;
}

.content {
	margin-bottom: 100px;
}
@font-face {font-family: "iconfont";
  src: url(/static/media/iconfont.42aef963.eot); /* IE9 */
  src: url(/static/media/iconfont.42aef963.eot#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
  url(/static/media/iconfont.9e2f45c4.woff) format('woff'),
  url(/static/media/iconfont.70ee3093.ttf) format('truetype'), 
  url(/static/media/iconfont.a82aee29.svg#iconfont) format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 42px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-miaosha:before {
  content: "\E60E";
}

.icon-xihuan:before {
  content: "\E687";
}

.icon-yingyong:before {
  content: "\E699";
}

.icon-shouye:before {
  content: "\E619";
}

.icon-gengduo:before {
  content: "\E738";
}



#like {
	background-color: #fff;
	font-size: 0;
	-webkit-text-size-adjust:none;
	padding-top: 3px;
}

#like p {
	font-size: 14px;
    color: #7f7f7f;
    padding-left: 5px;
}

.like_content {
	display: inline-block;
	width: 50%;
	text-align: center;
}

.like_link, .like_desc, .like_price{
	width: 95%;
	display: inline-block;

}

.like_desc {
	overflow: hidden;
	font-size: 12px;
	text-align: left;
	line-height: 14px;
	color: #333;
	padding-bottom: 5px;
	border-bottom: 1px solid #E5E6E6;
}

.like_desc span {
	display: -webkit-box;
	height: 28px;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2;
	overflow: hidden;
}

.like_price {
	margin: 4px 0 16px 0;
}

.like_price span {
	font-size: 14px;
	color: #f15353;
	display: table-cell;
	vertical-align: middle;
	float: left;
}

.like_price a {
	color: #999;
}

.like_price div {
	border: 1px solid #999;
	border-radius: 3px;
	display: inline-block;
	font-size: 12px;
	color: #999;
	padding: 2px 6px;
	float: right;
}



.oapp {
	height: 165px;
	background-color: #fff;
}

.oapp li {
	font-size: 12px;
	color: #666;
	text-align: center;
	width: 25%;
	float: left;
	margin-top: 12px;
}

.oapp li:last-child:after {
	content: "";
	display: block;
	clear: both;
}

.oapp a {
	color: #666;
}

.app_icon {
	width: 40px;
	height: 40px;
	margin: 0 auto 4px auto;
}

#spike {
	background-color: #fff;
	margin-top: 5px;
	padding: 5px;
}

.spike_header>i {
	display: inline-block;
	margin-left: 10px;
	margin-top: 4px;
	width: 18px;
	height: 22px;
	float: left;
	margin-top: 8px;
	background: url(/static/media/jd-sprites.1c93a187.png) -84px -109px no-repeat;
	background-size: 200px 200px;
}

.spike_title {
	white-space: nowrap;
    display: inline-block;
    float: left;
    font-size: 15px;
    color: #ca1327;
    margin: 10px 6px 0 5px;
}

.spike_time {
	display: inline-block;
	font-size: 13px;
	float: left;
	margin-top: 10px;
}

.spike_time span {
	display: inline-block;
	width: 18px;
	height: 18px;
	padding: auto;
	text-align: center;
	color: #fff;
	background: #3d3d3d;
	border-radius: 2px;
	padding-right: 2px;
	margin: 0 2px;
}

.spike_more {
	font-size: 12px;
	margin-top: 10px;
}

.spike_more span {
	color: #666;
}

.spike_more i {
	display: inline-block;
    width: 7px;
    height: 12px;
    margin: 2px 0 0 2px;
    background: url(/static/media/jd-sprites.1c93a187.png) -186px -51px no-repeat;
    background-size: 200px 200px;
}

.spike_more:after {
	content: "";
	display: block;
	clear: both;
}

.spike_content {
	margin-top: 10px;
	height: 150px;
}

.spike_content li {
	width: 33%;
	text-align: center;
	float: left;
}

.spike_content div {
	width: 100px;
	display: inline-block;
	border-right: 1px solid #f3f5f7;
}

.last_store {
	border: 0;
}

.spike_content img {
	width: 100%;
}

.spike_content p {
	margin-top: 8px;
	color: #ed5657;
	font-size: 14px;
	line-height: 14px;
}

.spike_content .real-price {
	color: #888;
    font-size: 10px;
    margin: 5px 0 12px 0;
    text-align: center;
    text-decoration: line-through;
}





img {
	width: 100%;
}

#more {
	background-color: #fff;
}

.more_link {
	width: 33%;
	border-width: 1px 1px 1px 0;
	border-style: solid;
	border-color: #f3f5f7;
	float: left;
}

.more_link:last-child {
	border-right: 0px;
}

.more_top:after, .more_middle:after, .more_bottom:after{
	content: "";
	display: block;
	clear: both;
}

.more_style {
	width: 49.8%;
	float: left;
}

.more_middle div:first-child {
	border-right: 1px solid #f3f5f7;
}

#more .swiper-pagination-bullet {
	width: 6px;
	height: 6px;
	border: 1px solid #fff;
	background-color: transparent;
	opacity: 1;
}

#more .swiper-pagination-bullet-active {
	background-color: #fff;
}

.more_bottom {
	padding: 8px;
	background-color: #f3f5f7;
}
